import { Box, Button, Stack, Typography } from '@mui/material'
import React from 'react'
import ButtonShare from './Button'
import image1 from "../asset/noon.jpg"
import image2 from "../asset/amazon.png"
import BasicTable from './BasicTable'
import PopupDiscount from './PopupDiscount'

function SubBody() {
  const [open, setOpen] = React.useState(false);
  // const [selectedValue, setSelectedValue] = React.useState(emails[1]);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);}
  return (
    <><Box sx={{marginTop:"5%"}}>
    <Typography component={"h2"}sx={{fontSize:"2rem",fontWeight:"600"}}>     4.1 </Typography>
    <Typography component={"p"} sx={{marginY:"50px"}}>           10                
 +   (    )    
   
         
    360 
   :        
</Typography>
    <Stack display={"flex"} flexDirection={"row"} justifyContent={"space-around"}>
    <ButtonShare text={"298 "} image={image1} onClick={handleClickOpen}/>
    <ButtonShare text={"298 "} image={image2} onClick={handleClickOpen}/>
    
    </Stack>
    <BasicTable/>
    <PopupDiscount open={open}handleClose={handleClose}/>
    </Box>
    </>
  )
}

export default SubBody